.header {
  min-height: 800px;
  background-image: url("../img/main-banner.png");
  border: 1px solid transparent; }

.header__wrap {
  margin: 0 auto;
  max-width: 1000px; }

.navbar {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  max-width: 1000px;
  margin-top: 20px; }
  .navbar .header__btn {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    border: none;
    text-transform: uppercase;
    background-color: #000;
    padding: 15px 20px;
    color: #fff;
    border-radius: 5px; }
  .navbar .logo {
    width: 33px;
    height: 30px;
    margin: auto 20px; }

.main__nav {
  margin: auto 10px; }

.menu__wrap {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  max-width: 770px; }

.nav__list {
  display: flex;
  margin: auto 20px;
  padding: 0; }

.nav__item {
  margin-left: 30px; }

.nav__link {
  text-transform: uppercase;
  color: #797b7c; }
  .nav__link:hover {
    color: #000000; }

.burger__svg {
  width: 20px;
  height: 12px; }

.burger__btn {
  border: none;
  background: transparent;
  margin: auto 20px; }

.header__info {
  margin: 200px 20px; }

.header__title {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 72px;
  font-weight: 200;
  text-transform: uppercase;
  color: #393a3a; }
  .header__title span {
    display: block; }

.header__subtitle {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  color: #393a3a; }

.start {
  margin-bottom: 100px; }
  .start__list {
    margin: 0 auto;
    list-style: none;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    max-width: 900px;
    justify-content: space-between; }
    .start__list .list__item {
      text-align: center;
      align-items: center;
      justify-content: flex-start;
      max-width: 250px;
      display: flex;
      flex-direction: column;
      font-family: 'Montserrat', sans-serif;
      font-weight: 300; }
      .start__list .list__item .item__svg {
        width: 60px;
        height: 60px;
        margin-bottom: 10px; }
      .start__list .list__item .item__title {
        font-size: 14px;
        min-height: 40px;
        max-width: 200px;
        margin-top: 0;
        margin-bottom: 10px; }
      .start__list .list__item .item__text {
        font-size: 14px;
        line-height: 22px;
        min-height: 100px;
        color: #a6a6a8;
        margin-top: 0;
        margin-bottom: 10px; }
      .start__list .list__item .item__link {
        font-size: 12px;
        text-transform: uppercase;
        color: #393a3a;
        margin-top: 0;
        margin-bottom: 10px; }

.start-banner {
  min-height: 900px;
  background-color: #26e6e6;
  background-image: url("../img/start-phone.png");
  background-position: 170px 75px;
  background-size: initial;
  background-repeat: no-repeat; }
  .start-banner__wrap {
    display: flex;
    justify-content: flex-end;
    max-width: 1120px;
    margin: 0 auto;
    padding: 10% 10%; }
  .start-banner__info {
    width: 390px;
    height: 675px;
    background-color: #fff; }

.start-banner__info {
  position: relative;
  box-sizing: border-box; }
  .start-banner__info .info__list {
    padding: 37px 3% 0 3%;
    margin: 0; }
    .start-banner__info .info__list .list__item {
      position: relative;
      padding: 0 0 0 25%;
      margin: 5% 5% 15% 5%;
      font-family: 'Montserrat', sans-serif;
      font-weight: 500;
      color: #393a3a; }
      .start-banner__info .info__list .list__item .icon {
        width: 54px;
        height: 54px;
        position: absolute;
        left: 0; }
      .start-banner__info .info__list .list__item .title {
        font-size: 18px; }
      .start-banner__info .info__list .list__item .text {
        font-size: 14px;
        color: #a6a6a8;
        line-height: 22px; }
  .start-banner__info .start__btn {
    position: absolute;
    bottom: 0;
    background-color: #000;
    width: 100%;
    height: 60px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    border: none;
    text-transform: uppercase;
    background-color: #000;
    padding: 15px 20px;
    color: #fff;
    border-radius: 0; }

.solutions {
  font-family: 'Montserrat', sans-serif; }
  .solutions__wrap {
